import React from 'react';
import { BiographyInner, BiographyInfo, BiographyInfoItem, BiographyInfoItemTitle, BiographyInfoItemContent, BiographyInfoLine, BiographySkill, BiographySkillsWrap } from '../../Styled';
import { JsIcon, ReduxIcon, SassIcon, NodeIcon, GatsbyIcon, GitIcon, AntIcon, GraphqlIcon, RestIcon, TypescriptIcon, MobxIcon, ReactIcon } from '../../Icons';
import { CircularProgressbarWithChildren } from 'react-circular-progressbar';
import AnimatedProgressProvider from './AnimatedProgressProvider'
import { easeLinear } from "d3-ease";
import SEO from '../../components/seo';

export default class Biography extends React.Component {

    biographyInfo = React.createRef();
    componentDidMount() {
        requestAnimationFrame(() => {
            window.scrollTo(0, this.biographyInfo.current.offsetTop)
        }, 0);
    }


    render() {
        return <>
            <SEO title={'ArminYahya - Skills'} description="Armin Yahya Skills" />
            <BiographyInner>
                <BiographyInfo ref={this.biographyInfo}>
                    <BiographyInfoItem>
                        <BiographyInfoItemTitle >
                            Birth Date
                    </BiographyInfoItemTitle>
                        <BiographyInfoLine />
                        <BiographyInfoItemContent>
                            1998-09-04
                    </BiographyInfoItemContent>
                    </BiographyInfoItem>
                    <BiographyInfoItem>
                        <BiographyInfoItemTitle>
                            Education
                    </BiographyInfoItemTitle>
                        <BiographyInfoLine />
                        <BiographyInfoItemContent>
                            Islamic Azad University-South Tehran
                    </BiographyInfoItemContent>
                    </BiographyInfoItem>
                    <BiographyInfoItem>
                        <BiographyInfoItemTitle>
                            Location
                    </BiographyInfoItemTitle>
                        <BiographyInfoLine />
                        <BiographyInfoItemContent>
                            <span>Iran-Tehran</span>
                        </BiographyInfoItemContent>
                    </BiographyInfoItem>
                </BiographyInfo>

                <BiographySkillsWrap>
                    <Skill name="Javascript" level={'advanced'} icon={JsIcon} />
                    <Skill name="Reactjs" level={'advanced'}  icon={ReactIcon} />
                    <Skill name="Sass" level={'advanced'}  icon={SassIcon} />
                    <Skill name="Mobx" level={'advanced'}  icon={MobxIcon} />
                    <Skill name="Redux" level={'intermediate'}  icon={ReduxIcon} />
                    <Skill name="Restful Api" level={'advanced'}  icon={RestIcon} />
                    <Skill name="Graphql" level={'intermediate'} icon={GraphqlIcon} />
                    <Skill name="Ant Design" level={'advanced'}  icon={AntIcon} />
                    <Skill name="Typescript" level={'advanced'}  icon={TypescriptIcon} />
                    <Skill name="Git" level={'advanced'}  icon={GitIcon} />
                    <Skill name="Gatsby" level={'advanced'}  icon={GatsbyIcon} />
                    <Skill name="Node" level={'intermediate'} icon={NodeIcon} />
                    <BiographySkill><div style={{ width: '25%' }} /></BiographySkill>
                    <BiographySkill><div style={{ width: '25%' }} /></BiographySkill>
                </BiographySkillsWrap>
            </BiographyInner>
        </>
    }
}

export function Skill({ name, level, icon: Icon }) {
    const percent = level === 'advanced' ? 90 : level === 'intermediate' ? 60 : 40;
    return (
        <BiographySkill>
            <div style={{ width: 80 }}>
                <AnimatedProgressProvider
                    valueStart={0}
                    valueEnd={percent}
                    duration={2}
                    easingFunction={easeLinear}
                    repeat={false}
                >
                    {(value) => (<CircularProgressbarWithChildren className="skill-percent" value={value} strokeWidth={2} duration={10}>
                        <Icon />
                    </CircularProgressbarWithChildren>)
                    }
                </AnimatedProgressProvider>
            </div>
            <span style={{ color: '#fff', marginTop: 10 }}>{name}</span>
            <span style={{ color: '#fff', fontSize: 12, marginTop: 5 }}>{level}</span>
        </BiographySkill>
    );
}